import { Collapse, Container } from "@material-ui/core";
import { Alert, AlertTitle } from "@material-ui/lab";
import React, { useState } from "react";

export default function ActionAlerts(): JSX.Element {
  const [open, setOpen] = useState(true);

  return (
    <Container maxWidth="md">
      <Collapse in={open}>
        <Alert severity="info" onClose={(): void => setOpen(false)}>
          <AlertTitle>
            尊敬的用户您好，感谢您使用蓝盟的服务！请仔细阅读以下条款：
          </AlertTitle>
          1. 蓝盟服务为无偿服务，用户无需为蓝客提供物质回报，自愿除外。
          <br />
          2. 原则上不提供拆机服务，若需拆机，后果需用户自行承担。
          <br />
          3. 您报修后，蓝客会主动联系您，您需要等待并配合蓝客的工作。
          <br />
          4. 蓝客完成维修后，请回到报修系统，为蓝客进行如实评价。
        </Alert>
      </Collapse>
    </Container>
  );
}
